import React from 'react';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';

const columns = [
  { field: 'id', headerName: 'ID', width: 90 },
  { field: 'programName', headerName: 'Program Name', width: 200 },
  { field: 'batchesName', headerName: 'Batches Name', width: 200 },
  { field: 'trainerName', headerName: 'Trainer Name', width: 150 },
  { field: 'numOfBatches', headerName: 'No. of Batches', width: 150 },
  { field: 'totalParticipants', headerName: 'Total Participants', width: 180 },
  { field: 'avgScorePre', headerName: 'Average Score (Pre)', width: 180 },
  { field: 'avgScorePost', headerName: 'Average Score (Post)', width: 180 },
  { field: 'view', headerName: 'View', width: 100 },
];

const rows = [
  {
    id: 1,
    programName: 'React Training',
    batchesName: 'Batch A, Batch B',
    trainerName: 'John Trainer',
    numOfBatches: 2,
    totalParticipants: 50,
    avgScorePre: 85,
    avgScorePost: 90,
    view: 'View Details',
  },
  {
    id: 2,
    programName: 'JavaScript Workshop',
    batchesName: 'Batch X, Batch Y, Batch Z',
    trainerName: 'Jane Instructor',
    numOfBatches: 3,
    totalParticipants: 75,
    avgScorePre: 78,
    avgScorePost: 85,
    view: 'View Details',
  },
  {
    id: 3,
    programName: 'Node.js Bootcamp',
    batchesName: 'Batch M, Batch N',
    trainerName: 'Bob Educator',
    numOfBatches: 2,
    totalParticipants: 40,
    avgScorePre: 92,
    avgScorePost: 88,
    view: 'View Details',
  },
  // Add more rows with similar structure
];

const TrainerWiseReport = () => {
  return (
    <div style={{ height: 400, width: '100%',marginTop:'40px'  }}>
    <DataGrid
      rows={rows}
      disableColumnFilter
      disableColumnSelector
      disableDensitySelector
      columns={columns}
      slots={{ toolbar: GridToolbar }}
      slotProps={{
        toolbar: {
          showQuickFilter: true,
        },
      }}
    />
  </div>
  );
};

export default TrainerWiseReport;
